.game-board {
  /* border: 2px solid black; */
  width: fit-content;
  height: fit-content;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  margin: 20px auto;
}

.border-top {
  box-shadow: inset 0px -2px 0px 0px #607d8b;
  border-radius: 0px;
}

.border-bottom {
  box-shadow: inset 0px 2px 0px 0px #607d8b;
  border-radius: 0px;
}

.border-left {
  box-shadow: inset -2px 0px 0px 0px #607d8b;
  border-radius: 0px;
}

.border-right {
  box-shadow: inset 2px 0px 0px 0px #607d8b;
  border-radius: 0px;
}

.corner {
  box-shadow: none;
}
